<template>
  <div id="container" class="mdl-card mdl-shadow--16dp">
    <div class="mdl-card__supporting-text">
      <cardFabTitle userTitle="Page.Privacy_policy"></cardFabTitle>
      <h5>1. OVERVIEW</h5>

      <p>1.1. Welcome to the websites, applications and services provided by pokerface.</p>

      <p>1.2 This policy is based on our goal to provide our visitors with quality information and resources while
        maintaining personal privacy. Please forward any comments you may have regarding this policy to
        privacy@pokerface.com.</p>

      <p>1.3 By visiting the Sites, registering with us or contacting, you signify that you understand and agree to the
        collection, use and transfer of your information (including your personal data) under the terms of this policy.
        If
        you do not accept, or do not wish to be bound by this policy, please do not use the Sites.</p>

      <p>1.4 "Personal data" is any information that enables us to identify you, directly or indirectly, such as your
        email
        address, name, shipping and billing address, telephone number, financial information, user names, any form of
        identification number or one or more factors specific to your physical, physiological, mental, economic,
        cultural
        or
        social identity.</p>

      <p>1.5 For the purposes of the EU General Data Protection Regulation 2016/679 (the "GDPR"), the data controller is
        pokerface.</p>

      <h5>2. INFORMATION WE COLLECT FROM YOU</h5>

      <p>2.1 When you access our Sites, we collect different types of information, described as "access information",
        "contact information" and "third party information", below. Some of the information we collect will be personal
        data
        relating to you, and we will be subject to certain obligations in respect of such personal data.</p>

      <h5>3. ACCESS INFORMATION</h5>

      <p>3.1 We may collect information about your computer, including where available your geographic location, IP
        address,
        operating system and browser type, device type, bandwidth, etc. for system administration and to report
        aggregate
        information to our advertisers. We will only share anonymised statistical data about our users' browsing actions
        and
        patterns from the Sites which cannot be used to identify any individual.
      </p>

      <p>3.2 We may also keep details of your visits to our Sites including, but not limited to, traffic data, location
        data,
        weblogs and other communication data, time spent on the Sites, number of pages viewed, referring web pages, the
        resources that you access and the date and time of access, etc. To the extent this information can identify you
        directly, or indirectly when combined with other information, it will be personal data and will be subject to
        this
        privacy policy.
      </p>

      <p>3.3 Our Sites use cookies, web beacons, and other technologies in order to provide you with an improved
        browsing
        experience, to allow us to improve our Sites and to serve targeted advertising. We may collect advertising
        information, including but not limited to, type of ads seen, number of times ads were served, pages where the
        ads
        were
        served, etc. When you first access the Sites, depending on your location, you may receive a message advising you
        that
        cookies and similar technologies are in use. By clicking "accept", closing the message, or continuing to browse
        the
        Sites, you signify that you understand and agree to the use of cookies in accordance with our Cookies Policy.
      </p>

      <h5>4. CONTACT INFORMATION</h5>

      <p>4.1 When you contact us via the Sites or otherwise (for example, by clicking a link to send us an email or if
        you
        telephone or write to us), you may provide certain personal data about yourself including your name, contact
        details and address which will be stored by us.
      </p>

      <p>4.2 You may also give us information about you by filling in forms on the Sites. This includes personal data
        you
        provide when you register to use our Sites, subscribe to our services, participate in discussion boards or other
        social media functions on our Sites, enter a competition, promotion or survey and when you report a problem with
        our
        Sites. The personal data you give us may include your name, address, e-mail address and phone number, financial
        and
        credit card information, personal description and photograph.
      </p>

      <p>4.3 You can also create an account on our Sites and subscribe to our services using your social media profile.
        If
        you
        select this option, we will automatically collect selected personal data about you from your social media
        account.
        The
        personal data we collect depends on your social media privacy settings for sharing such personal data, and may
        include
        name, profile picture, age range, gender, language, country and other public information.
      </p>

      <h5>5. THIRD PARTY INFORMATION</h5>

      <p>5.1 We may also receive information about you if you use any of the other websites we operate or the other
        services
        we
        provide. We also work closely with third parties (including, for example, business partners, sub-contractors in
        technical, payment and delivery services, search information providers, credit reference agencies, advertising
        networks, advertising exchanges, data management platforms, content recommendation platforms and analytics
        providers)
        and may receive information about you from them. Depending on its nature, this third-party information will
        either
        be
        "access information" or "contact information", as described above. We will take reasonable steps to notify you
        where
        your personal data is received from third parties as required.
      </p>

      <h5>6. THE WAY WE USE PERSONAL DATA</h5>

      <p>6.1 We use your personal data according to the terms of the privacy policy in effect at the time of our use. We
        will process your personal data where:</p>
      <ul>
        <li>6.1.1 you have provided your consent which can be withdrawn at any time;</li>

        <li>6.1.2 where the processing is necessary for the performance of a contract to which you are a party or to
          perform
          services you have requested;
        </li>

        <li>6.1.3 where we are required by law;</li>

        <li>6.1.4 where processing is required to protect your vital interests or those of another person; or</li>

        <li>6.1.5 where the processing is necessary for the purposes of our legitimate commercial interests, except
          where
          such
          interests are overridden by your rights and interests.
        </li>
      </ul>

      <p>
        6.2 Your "access information" is used to route the requested web page to your computer or mobile device for
        viewing.
        It enables us to provide you with access to our Sites. We use this information in line with our legitimate
        commercial
        interests to administer our Sites and for internal operations, including troubleshooting, data analysis, site
        analysis, testing, research, statistical and survey purposes, to improve our Sites, to ensure that content is
        presented in the most effective manner for you and for your device, to allow you to participate in interactive
        features of our services, when you choose to do so, as part of our efforts to keep our Sites safe and secure; to
        measure or understand the effectiveness of advertising we serve to you and others, and to deliver relevant
        advertising
        to you, and where you have provided your consent, to make suggestions and recommendations to you about goods or
        services that may interest you.</p>

      <p>
        6.3 Your "contact information" enables us to provide services to you, or information tailored more specifically
        to
        your needs, or to forward your message or enquiry to another entity that is better able to do so. As such, it
        may
        enable us or our selected partners to contact you, for example, to respond to a query you have submitted to us.
