        will also use and analyse the information we collect so that we can administer, support, improve and develop our
        business, customer service and the features of our Sites and our services generally. We will not share your
        personal
        data for marketing purposes without your consent.
      </p>
      <p>
        6.4 The "third party information" we receive will be combined with access information and/or contact
        information.
        We
        will use this personal data and the combined personal data for the purposes set out above (depending on the
        types
        of
        personal data we receive).
      </p>
      <p>
        6.5 We will use the personal data you provide to us to contact you by email, telephone, post and/or fax to
        notify
        you
        occasionally about important changes or developments to the Sites. Where you have provided the appropriate form
        of
        consent, we may also use your personal data to let you know by email, telephone, post and/or fax about other
        goods
        and
        services which we, or our selected partners, offer which may be of interest to you.
      </p>
      <p>
        6.6 We will ask you to give your consent to the use of your personal data for marketing purposes, and to provide
        you
        with information about our goods and services, or those of third parties, which we believe, may be of interest
        to
        you.
        If you change your mind about being contacted by selected third parties or us, please click "unsubscribe" from
        any
        emails received.
      </p>
      <p>
        6.7 With your opt-in consent, we may also permit selected third parties to use your personal data, to provide
        you
        with
        information about goods and services which may be of interest to you. We or they may contact you about these by
        email.
        In the event that you are given (or where you have chosen) a password which enables you to access certain parts
        of
        the
        Sites, you are responsible for keeping this password confidential. You must not share a password with anyone.
      </p>
      <h5>7. DISCLOSURE OF YOUR PERSONAL DATA</h5>

      <p>7.1 Where necessary, we will share your personal data for the purposes described in this privacy policy
        with:</p>
      <ul>
        <li>7.1.1 members of our group, which means our subsidiaries, our ultimate holding company and its subsidiaries;
        </li>
        <li>

          7.1.2 business partners, suppliers and sub-contractors who require access to personal data to assist in the
          performance of any contract we enter into with them or you;
        </li>
        <li>

          7.1.3 trusted third-party companies and individuals to perform functions in certain situations on our behalf.
          These
          functions include cloud storage providers, email services, providing marketing assistance and data analysis,
          handling
          transactions, fulfilling orders and providing customer service and credit referencing. These agents have
          access
          to
          the
          personal data required in order to perform their functions but may not use it for any other purpose; and
        </li>
        <li>

          7.1.4 analytics and search engine providers that assist us in the improvement and optimisation of our Site.
        </li>
      </ul>
      <p>7.2 For personal data from the European Union or the European Economic Area (the "EEA"):
      </p>
      <ul>
        <li>7.2.1 We will only transfer your personal data to trusted third-parties who provide sufficient security
          guarantees
          and
          who demonstrate a commitment to compliance with applicable law and this privacy policy.
        </li>
        <li>
          7.2.2 We will not transfer personal data originating from the EEA to third parties located outside of the EU
          without
          ensuring adequate protection under European law.
        </li>
        <li>
          7.2.3 Where transfer is to a third party is located in a country not recognised by the EU Commission as
          ensuring
          an
          adequate level of protection, we will take appropriate steps, such as enforcing standard contractual clauses
          recognised by the EU Commission, to safeguard your personal data and to ensure legally enforceable remedies
          are
          available.
        </li>
      </ul>
      <p>7.3 We may also disclose your personal data to third parties:
      </p>
      <ul>
        <li>
          7.3.1 where we are required to do so by law;
        </li>
        <li>
          7.3.2 in the event that we sell or buy any business or assets, in which case we may disclose your personal
          data
          to
          the
          prospective seller or buyer of such business or assets; or
        </li>
        <li>
          7.3.3 to exchange other information with other companies and organisations for fraud protection and credit
          risk
          reduction.
        </li>
      </ul>
      <p>7.4 Unless required to do so by law, we will not otherwise share, sell or distribute any of the personal data
        you
        provide to us without your consent.
      </p>
      <h5>8. PROVIDING INFORMATION IS YOUR CHOICE</h5>
      <p>
        8.1 There is no legal requirement for you to provide any information at our Sites. However, our Sites will not
        work
        without certain "access information" described above. Failure of your browser to accept "cookies" will not
        prevent
        your use of our Sites but may prevent certain features from working.
      </p>
      <p>
        8.2 For any "contact information" that is requested on a Site, failure to provide the requested information will
        mean
        that the particular feature or service associated with that part of the Site may not be available to you.
      </p>
      <h5>9. HOW LONG WE STORE YOUR PERSONAL DATA</h5>

      <p>
        9.1 We will only retain your personal data, in a form which permits us to identify you, for as long as necessary
        to
        fulfil the purposes we collected it for. We will retain and use your personal data as necessary to satisfy any
        legal,
        accounting or reporting requirements, to resolve disputes or to enforce our agreements and rights. In line with
        this
        policy, we will either securely delete or anonymize your personal data so that it cannot be linked back to you.
      </p>
      <h5>10. OUR COMMITMENT TO DATA SECURITY</h5>

      <p>
        10.1 The transmission of information via the internet is not completely secure. Although we will do our best to
        protect your personal data and implement appropriate technical and organisational security measures, we cannot
        guarantee the security of your data transmitted to our Sites; any transmission is at your own risk. Once we have
        received your information, we will use procedures and security features to try to prevent unauthorised access.
        To
        prevent unauthorised access, maintain data accuracy, and ensure the correct use of information, we have put in
        place
        appropriate safeguards to secure the information we collect online, consistent with the policies of Insider Inc.
        and
        industry standards.
      </p>
      <p>
        10.2 Our Sites may, from time to time, contain links to and from the websites of our partner networks,
        advertisers
        and
        affiliates. If you follow a link to any of these websites, please note that these websites have their own
        privacy
        policies and that we do not accept any responsibility or liability for these policies. Please check these
        policies
        before you submit any personal data to these websites.
      </p>
      <h5>11. YOUR EUROPEAN PRIVACY RIGHTS</h5>

      <p>
        11.1 For individuals within the EEA only. Under the GDPR, in certain circumstances, you have the right to (a)
        request
        access to any personal data we hold about you and related information, (b) obtain without undue delay the
        rectification of any inaccurate personal data, (c) request that your personal data is deleted provided the
        personal
        data is not required by us for compliance with a legal obligation under European or Member State law or for the
        establishment, exercise or defence of a legal claim, (d) prevent or restrict processing of your personal data,
        except
        to the extent processing is required for the establishment, exercise or defence of legal claims; and (e) request
        transfer of your personal data directly to a third party where this is technically feasible.
      </p>
      <p>
        11.2 In addition, where you believe that we have not complied with its obligation under this privacy policy or
        European law, you have the right to make a complaint to an EU Data Protection Authority, such as the UK
        Information
        Commissioner's Office.
      </p>
      <p>
        11.3 If you would like to exercise any of these rights, please click here.
      </p>
      <h5>12. EMAIL POLICY</h5>

      <p>
        12. 1 Insider Inc. offers email newsletters. If you no longer wish to receive emails, follow the "unsubscribe"
        instructions located near the bottom of each newsletter.
      </p>
      <p>
        12.2 We will not share, sell, rent, swap or authorize any third party to use your email address for commercial
        purposes without your permission.
      </p>
      <h5>13. CHILDREN UNDER 16</h5>
      <p>
        13. 1 The Sites are not intended for children under 16 years of age. No one under age 16 may provide any
        information to or on the Sites. Insider Inc. does not knowingly target advertising to, or collect information
        from,
        children under the age of 16. If you are under 16, do not use or provide any information on the Sites or on or
        through any
        of its features, register on the Sites, use any of the interactive or public comment features on the Sites, or
        provide any information about yourself to us, including your name, telephone number, email address or any screen
        name or
        user name you may use. If we learn we have collected or received personal information from a child under 16
        without
        verification of parental consent, we will delete that information. If you believe we might have any information
        from or
        about a child under 16, please contact us at privacy@pokerface.com.
      </p>
      <h5>14. GENERAL</h5>

      <p>
        14.1 We may edit this policy from time to time. Please check this policy regularly for any changes.
      </p>
      <p>
        14.2 Should you have other questions or concerns about our privacy policies and practices, please contact
        us at privacy@pokerface.com.</p>

    </div>
  </div>
</template>

<script>
  import PageBase from '@/components/pages/Page'
